.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 0.5rem 1rem;
  }
  
  .navbar-links a {
    color: #fff;
    margin: 0 0.5rem;
    text-decoration: none;
  }
  
  .navbar-links a:hover {
    text-decoration: underline;
  }